@import url(//netdna.bootstrapcdn.com/font-awesome/3.2.1/css/font-awesome.css);
.userDropdown {
  display: none;
}

.userDropdownBtn:hover .userDropdown {
  display: block;
  display: flex;
  flex-direction: column;
  transition: all 1s;
}

.slider {
  -webkit-appearance: none;
  height: 12px;
  border-radius: 5px;
  background: #d3d3d3;
  outline: none;
  transition: opacity 0.2s;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 18px;
  height: 18px;
  border-radius: 75%;
  background: #b7791f;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 18px;
  height: 18px;
  border-radius: 75%;
  background: #b7791f;
  cursor: pointer;
}
* {
    box-sizing: border-box;
  }
  
  .App {
    margin: 0 auto;
    width: 1000px;
  }
  
  .btn {
    background: #fff;
    border: solid 1px rgba(0, 0, 0, 0.1);
    padding: 10px 20px;
    font-size: 14px;
    border-radius: 5px;
  }
  
  a.btn {
    text-decoration: none;
    color: #000;
  }
  
  .btn:hover {
    cursor: pointer;
    border-color: #000;
  }
  
  .navbar {
    padding: 30px 0;
  }
  
  .navbar ul {
    margin: 0;
    padding: 0;
  }
  
  .navbar ul li {
    display: inline-block;
    width: calc(100% / 3);
  }
  
  .navbar ul li {
    text-align: center;
  }
  
