  }
  
  .bookmark p {
    font-size: 16px;
    color: rgba(0, 0, 0, 0.8);
    margin: 0;
  }
  
  .bookmark .number {
    position: absolute;
    left: 0;
    background-color: #000;
    color: #fff;
    font-size: 11px;
    font-weight: 900;
    height: 25px;
    width: 25px;
    border-radius: 100%;
